@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


@tailwind base;
@tailwind components;
@tailwind utilities;


@layer base {
    html {
        font-family: "Poppins", "sans-serif";
    }

    body {
        background-color: #fdfeff;
        overflow-x: hidden;
    }
}

@layer components {
    .input-box {
        @apply w-full text-sm bg-transparent border-[1.5px] px-5 py-3 rounded mb-4 outline-none;
    }

    .btn-primary{
        @apply w-full text-sm bg-primary text-white rounded my-1 hover:bg-blue-500  py-2
    }

    .icon-btn {
        @apply text-xl text-slate-300 cursor-pointer hover:text-primary;
    }

    .input-label {
        @apply text-xs text-slate-400
    }
}